<template>
  <!-- 车辆信息 -->
  <div class="container">
    <div class="elStepsCon">
      <el-steps :active="active" finish-status="success">
        <el-step title="公司审核"></el-step>
        <el-step title="客服审核"></el-step>
        <el-step title="管理办审核"></el-step>
      </el-steps>
    </div>
    <div class="carInfo">
      <div class="item">
        <div>车牌号</div>
        <span>陕A8888888</span>
      </div>
      <div class="item">
        <div>车辆所有人</div>
        <span>陕A8888888</span>
      </div>
      <div class="item">
        <div>驾驶员</div>
        <span>陕A8888888</span>
      </div>
      <div class="item">
        <div>驾驶员电话</div>
        <span>陕A8888888</span>
      </div>
      <div class="item">
        <div>ETC编码</div>
        <span>陕A8888888</span>
      </div>
      <div class="item">
        <div>ETC内码</div>
        <span>陕A8888888</span>
      </div>
      <div class="item">
        <div>企业担保人</div>
        <span>陕A8888888</span>
      </div>
      <div class="item">
        <div>车辆检查</div>
        <span>陕A8888888</span>
      </div>
      <div class="item">
        <div>车辆类型</div>
        <span>陕A8888888</span>
      </div>
      <div class="item itemAll">
        <div>行驶证</div>
        <img src="./../assets/logo.png" class="logo" alt="" />
      </div>
      <div class="item itemAll">
        <div>车辆照片</div>
        <img src="./../assets/logo.png" class="logo" alt="" />
      </div>
    </div>
    <div class="operating">
      <el-button type="danger" @click="reject = true">驳回审核</el-button>
      <el-button type="primary" @click="next">通过审核</el-button>
    </div>
    <el-dialog
      title="驳回申请反馈"
      :visible.sync="reject"
      width="30%"
      :before-close="handleClose">
      <div>
        
        <el-input
          type="textarea"
          :autosize="{ minRows: 2, maxRows: 4}"
          placeholder="请输入驳回理由"
          v-model="textarea2">
        </el-input>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="reject = false">取 消</el-button>
        <el-button type="primary" @click="reject = false">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "carInfo",
  data() {
    return {
      reject: false,
      active: 0,
    };
  },
  methods: {
    next() {
      this.$message({
        message: '审核通过',
        type: 'success'
      });
      if (this.active++ > 2) this.active = 0;
    },
  },
};
</script>
<style scoped>
.container {
  background-color: #fff;
  height: 100%;
  overflow: auto;
  padding: 20px 0;
}
.elStepsCon {
  width: 400px;
  margin: 20px auto;
}
.operating {
  margin-left: 20px;
}
.carInfo {
  padding: 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: row;
  flex-wrap: wrap;
}
.carInfo .item {
  width: 50%;
  padding: 10px 0;
  font-size: 15px;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.itemAll {
  width: 60% !important;

}
.itemAll  img{
  width: 150px;
  height: 150px;
}
.carInfo .item div {
   width: 20.33333333%;
   font-size: 15px;
}
span {
  color: #999;
}
</style>


